.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;

  &__list {
    margin: 0 15px;
    display: flex;
    justify-content: center;
  }

  &__elem {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__btn {
    border: 1px solid transparent;

    &:hover {
      background: rgb(172, 9, 9);
      transition: all ease .2s;
    }

    &.active {
      border: 1px solid red;
      color: #fff;
      background: #000;
    }
  }
}