.title {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 30px;
  font-size: 30px;
}

.checkbox {
  display: flex;
  align-items: center;

  div {
    width: auto;
    display: inline-block;
  }

  input,
  label {
    cursor: pointer;
  }
}

.input_item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  input {
    &::placeholder {
      color: #a4a4a4;
    }
  }
}

.checkbox_input {
  display: inline-block;
  width: auto;
}

.error_field {
  text-transform: uppercase;
  font-size: 12px;
  color: red;

  &:not(:last-child) {
    margin-bottom: 10px;
  }
}