.post {
  &:not(:last-child) {
    margin-bottom: 30px;
  }

  &__body {
    display: flex;
    align-items: center;
  }

  &__photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #fff;
    margin-right: 20px;

    img {
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
    }

  }

  &__text {
    width: 100%;
    background: #fff;
    border-radius: 100px;
    padding: 20px 30px;
    color: #000;
  }

  &__footer {}
}

.like {
  margin-left: auto;
  margin-right: 0;
}