.field {
  position: relative;
  width: 100%;

  &:not(:last-child) {
    margin-right: 10px;
  }

  &.error {
    input {
      background: rgb(250, 100, 100);

      &::placeholder {
        color: #fff;
      }
    }
  }

}



.input {
  display: block;
  width: 100%;
}

.error_text {
  // display: none;
  font-size: 10px;
  color: red;
  position: absolute;
  bottom: -15px;
  left: 0;
  white-space: nowrap;
}