$md1: 1440px;
$md2: 1200px;
$md3: 992px;
$md4: 767px;
$md5: 568px;
$md6: 412px;

$colorGrey:rgb(54, 54, 54);

html,
body {
  height: 100%;
  box-sizing: border-box;

}


*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

body {
  background: $colorGrey;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2rem;
  color: #2e2e2e;
  overflow-x: hidden;
  color: #fff;

  &.overlay {
    height: 100vh;
    overflow: hidden;
  }
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
}

input {
  display: block;
  width: 100%;
  background: $colorGrey;
  border: 1px solid red;
  padding: 7px 10px 6px 10px;
  border-radius: 5px;
  color: #fff;

  &::placeholder {
    color: rgb(155, 1, 1);
  }
}

.btn {
  cursor: pointer;
  border-radius: 5px;
  display: block;
  background: red;
  color: #000;
  border: none;
  outline: none;
  white-space: nowrap;
  font-size: 15px;
  text-align: center;
  padding: 7px 10px 6px 10px;

  &:disabled {
    background: rgb(139, 139, 139);
    color: #2e2e2e;
    border-color: rgb(139, 139, 139);
  }
}

.preloader {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99;
}