.user {
  display: flex;
  align-items: center;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &__left {
    margin-right: 20px;
  }

  &__photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 5px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__btn {
    display: block;
    margin: 0 auto;
  }

  &__right {
    width: 100%;
    padding: 20px;
    border: 1px solid red;
    border-radius: 20px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
  }

  &__name {
    font-size: 20px;
  }

  &__location {
    font-size: 18px;

    span {
      display: block;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}