.friend {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 2.5%;
  margin-bottom: 2.5%;
  width: 45%;
  padding: 15px;
  background: rgb(136, 133, 133);
  border-radius: 50px;

  &__photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 10px;
    overflow: hidden;

    img {
      display: block;
      object-fit: cover;
      object-position: 15% 7%;
      width: 100%;
      height: 100%;
    }
  }

  &_name {
    font-size: 20px;
  }
}