.user {
  &__wrapper {
    padding: 10px 20px;
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    width: 100%;
    background: rgb(54, 54, 54);
  }

  &__photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 20px;

    img {
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
    }
  }
}